














import {
  computed, defineComponent, onMounted, onUnmounted, ref, watch,
} from '@vue/composition-api';
import NoticeItem from '@/app/website/client/view/component/notice/NoticeItem.vue';
import useI18N from '@/app/shared/client/logic/i18n';
import {
  ACCEPT_TRACKING_USER_RESPONSE,
  DECLINE_TRACKING_USER_RESPONSE,
  useTrackingConsent,
} from '@/app/website/client/logic/tracking-consent';

const GDPRBannerTile = defineComponent({
  components: {
    NoticeItem,
  },
  setup(_, { root, emit }) {
    const i18n = useI18N(root.$store);

    const trackingConsent = useTrackingConsent(root.$store);

    const visibility = ref(false);

    const withoutAccent = computed(() => trackingConsent.askWithoutAccent.value);

    const accept = (): void => trackingConsent.setUserResponse(ACCEPT_TRACKING_USER_RESPONSE);
    const decline = (): void => trackingConsent.setUserResponse(DECLINE_TRACKING_USER_RESPONSE);

    onMounted(() => {
      emit('tile-loaded');

      watch(trackingConsent.userResponse, () => {
        visibility.value = !trackingConsent.userResponseReceived.value;
      }, { immediate: true });
    });

    onUnmounted(() => {
      emit('tile-unloaded');
    });

    return {
      i18n,
      visibility,
      withoutAccent,
      accept,
      decline,
    };
  },
});

export default GDPRBannerTile;
