










































































































import {
  computed, defineComponent, ref, Ref, toRefs,
} from '@vue/composition-api';
import { isNullOrEmpty } from '@/app/shared/client/helper/base';
import CrossIcon from '@/app/website/client/view/icon/CrossIcon.vue';
import NoticeErrorIcon from '@/app/website/client/view/icon/NoticeErrorIcon.vue';
import NoticeSuccessIcon from '@/app/website/client/view/icon/NoticeSuccessIcon.vue';
import NoticeWarningIcon from '@/app/website/client/view/icon/NoticeWarningIcon.vue';
import NoticeInfoIcon from '@/app/website/client/view/icon/NoticeInfoIcon.vue';

function useCloseHandler(
  closed: Ref<boolean>,
): () => void {
  function close(): void {
    closed.value = true;
  }

  return close;
}

function usePrimaryActionHandler(
  emit: (event: string) => void,
): () => void {
  function primaryAction(): void {
    emit('primary-action');
  }

  return primaryAction;
}

function useSecondaryActionHandler(
  emit: (event: string) => void,
): () => void {
  function secondaryAction(): void {
    emit('secondary-action');
  }

  return secondaryAction;
}

const NoticeItem = defineComponent({
  components: {
    CrossIcon,
    NoticeErrorIcon,
    NoticeSuccessIcon,
    NoticeWarningIcon,
    NoticeInfoIcon,
  },
  props: {
    visibility: {
      type: Boolean,
      required: false,
      default: false,
    },
    error: {
      type: Boolean,
      required: false,
      default: false,
    },
    success: {
      type: Boolean,
      required: false,
      default: false,
    },
    warning: {
      type: Boolean,
      required: false,
      default: false,
    },
    info: {
      type: Boolean,
      required: false,
      default: false,
    },
    title: {
      type: String,
      required: false,
      default: undefined,
    },
    text: {
      type: String,
      required: false,
      default: undefined,
    },
    primaryActionText: {
      type: String,
      required: false,
      default: undefined,
    },
    secondaryActionText: {
      type: String,
      required: false,
      default: undefined,
    },
    sameActionsType: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  setup(props, { emit }) {
    const {
      title, text, primaryActionText, secondaryActionText,
    } = toRefs(props);

    const hasTitle = computed(() => !isNullOrEmpty(title.value));
    const hasText = computed(() => !isNullOrEmpty(text.value));
    const hasPrimaryAction = computed(() => !isNullOrEmpty(primaryActionText.value));
    const hasSecondaryAction = computed(() => !isNullOrEmpty(secondaryActionText.value));

    const closed = ref(false);
    const close = useCloseHandler(closed);

    const primaryAction = usePrimaryActionHandler(emit);
    const secondaryAction = useSecondaryActionHandler(emit);

    return {
      hasTitle,
      hasText,
      hasPrimaryAction,
      hasSecondaryAction,
      closed,
      close,
      primaryAction,
      secondaryAction,
    };
  },
});

export default NoticeItem;
